body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.sidebar{
    flex: 0.8 1;
    height: 100%;
    background-color: #15092d;
    position: -webkit-sticky;
    position: sticky;
    top: 10px; 
}

.sidebarWrapper{
    padding: 10px;
    color: white;
}

.sidebarMenu{
    margin-bottom: 50px;
}

.sidebarList{
    font-family: Open sans;
    color: white;
    font-size: 14px;
    margin-top: 5px;
    padding: 5px;
    list-style: none;
}


.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
}

/* .sidebarListItem.active,
.sidebarListItem:hover{
    background-color: red;
} */

.siderbarIcon{
    margin-right: 5px;
    font-size: 20px !Important;
    /* flex-direction: row; */
    
}
.SpeedIcon{
    visibility: hidden;
}


.topbar{
    width:100%;
    height: 50px;
    background-color: #15092d;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    
}

.topbarWrapper{
    height: 100%;
    padding: 13px 20px;
    display: flex;
    color:white;
    /* justify-content: space-between; */
    text-transform: uppercase;
}

.logo{
    font-weight: bold;
    font-size: 15px;
    color: white;
    cursor: pointer;
    margin: 0 0 20px;
    height: 20px;
}

.topLeft{
    align-items: left;
}

.topbarIconContainer{
    position: relative;
}

.topRight{
    display: flex;
    align-items: right;
   
}

.topbarIconContainer{
    position: relative;
    cursor: pointer;
    margin: 10px;
    margin: right;
    color: #555;
}

.icon{
    align: right;
}

.topAvatar{
    width: 40px;
    height: 40px;
    border-radius: 70%;
    align: left;
}
.container{
  display: flex;
}

.IconLabelButtons{
  display: flex;
}
.list{
    display: flex;
    width: 100%;
    .listContainer{
        flex: 6 1;
    }
}
.table{
    .cellWrapper{
        display: flex;
        align-items :center;
    
    .image{
        width: 32px;
        height: 322px;
        border-radius: 50%;
        margin-right: 10px;
    }
    }
}

.display{
  table-layout: 500 !important;
  margin: 10px;
}

.cellWrapper{
    align-items: center;
    font-size: 14px;
    margin-top: 12px;
}


.home{
    flex: 3 1;
    background-color: #E5E5E5;
    
}

.IconLabelButtons{
    display: flex;
}

.topic{
    margin-top: 6px;
    border-radius: 1;
